import React, { useState, useEffect } from 'react';
import { motion, useAnimation } from 'framer-motion';
import ReactGA from 'react-ga';

import logo from 'assets/codse_logo.png';
import { Link } from 'react-router-dom';

const items = [
  { title: 'we build iOS apps', color: '#5fc9f8' },
  { title: 'we develop android apps', color: '#a4c639' },
  { title: 'we create eCommerce apps', color: '#21759b' },
  { title: 'we automate repeated tasks', color: '#dd4b39' },
  { title: 'we architect software systems', color: '#ffc43e' }
];

ReactGA.initialize('UA-161968662-1');
ReactGA.pageview('/');

const Home = () => {
  const [current, setCurrent] = useState(0);
  const controls = useAnimation();
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((current + 1) % items.length);
    }, 5000);
    return () => clearInterval(timer);
  }, [current]);

  useEffect(() => {
    controls.start({
      marginTop: -current * 60
    });
  }, [controls, current]);

  return (
    <>
      <section
        className="text-center hero is-fullheight"
        style={{ alignItems: 'center' }}
      >
        <div className="hero-body">
          <div
            className="container"
            style={{ margin: '0 auto', textAlign: 'center' }}
          >
            <img
              alt="Codse Tech"
              src={logo}
              style={{ maxWidth: 500, width: '100%', margin: 16 }}
            />
            <h1
              className="title is-1"
              style={{
                color: '#9A56B6',
                textAlign: 'center',
                marginBottom: 16
              }}
            >
              Codse
            </h1>
            <motion.div id="dev-container">
              <div id="dev-anim">
                <motion.div animate={controls} id="we-develop">
                  {items.map(item => (
                    <div key={item.title}>
                      <strong style={{ color: item.color }}>
                        {item.title}
                      </strong>
                    </div>
                  ))}
                </motion.div>
              </div>
            </motion.div>
            <div id="opn" style={{ marginTop: 64 }}>
              <span role="img" aria-label="Opening">
                🔥🔥
              </span>{' '}
              &nbsp; Scroll down to see our job openings &nbsp;
              <span role="img" aria-label="Opening">
                🔥🔥
              </span>
            </div>
          </div>
        </div>
      </section>
      <section
        className="text-center hero is-fullheight"
        style={{ alignItems: 'center' }}
        id="#opening"
      >
        <div className="hero-body">
          <div
            className="container"
            style={{ margin: '0 auto', textAlign: 'center' }}
          >
            <h2 className="subtitle is-2">Current openings:</h2>
            <div id="career-cards" className="columns" style={{ margin: 0 }}>
              <div className="column">
                <div className="card job" style={{ borderColor: '#f55247' }}>
                  <Link
                    to="/career/full-stack-developer"
                    onClick={() =>
                      ReactGA.pageview('/career/full-stack-developer')
                    }
                    title="Learn more"
                  >
                    Learn more
                  </Link>
                  <p>#Laravel #Backend #Frontend #React</p>
                  <div
                    className="content"
                    style={{ backgroundColor: '#f55247' }}
                  >
                    Full-Stack Developer
                  </div>
                </div>
              </div>
              <div className="column">
                <div className="card job" style={{ borderColor: '#00a8dd' }}>
                  <Link
                    to="/career/react-native-developer"
                    onClick={() =>
                      ReactGA.pageview('/career/react-native-developer')
                    }
                    title="Learn more"
                  >
                    Learn more
                  </Link>
                  <p>#React #Redux #Android #iOS #Expo</p>
                  <div
                    className="content"
                    style={{ backgroundColor: '#00a8dd' }}
                  >
                    React Native Developer
                  </div>
                </div>
              </div>
              <div className="column">
                <div className="card job" style={{ borderColor: '#fcaf45' }}>
                  <Link
                    to="/career/content-manager"
                    onClick={() => ReactGA.pageview('/career/content-manager')}
                    title="Learn more"
                  >
                    Learn more
                  </Link>
                  <p>#Facebook #Instagram #LinkedIn #Content</p>
                  <div
                    className="content"
                    style={{ backgroundColor: '#fcaf45' }}
                  >
                    Content Manager
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
